body {
  background: #f3f3f3;
}

.css-1oezt9k-MenuDialog,
.css-16zezay {
  background: #ddd !important;
}

/* editor, animation question blocks */
.item-enter {
  opacity: 0.01;
  -webkit-transform: scale(0);
          transform: scale(0);
}
.item-enter-active {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: all .3s ease-in;
}
.item-exit {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.item-exit-active {
  opacity: 0.01;
  -webkit-transform: scale(0);
          transform: scale(0);
  transition: all .3s ease-in;
}
/* parsed pages, animation error messsages */
.toggle-enter {
  height: 0px;
}
.toggle-enter-active {
  height: 20px;
  transition: all .3s ease-out;
}
.toggle-exit {
  height: 20px;
}
.toggle-exit-active {
  height: 0px;
  
  transition: all .3s ease-out;
}
